h1, h2, h3, h4, h5, .head-text-style {
  font-family: 'Lato', sans-serif;
}

* {
  font-family: 'Merriweather', serif;
}

.highlighted-text{
  color: white;
  font-weight: bold;
}

/* https://www.w3schools.com/howto/howto_css_custom_scrollbar.asp */
body::-webkit-scrollbar, .resume::-webkit-scrollbar {
  width: 20px;
  padding-left: 10px;
}
body::-webkit-scrollbar, .resume::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(128, 128, 128); 
  border-radius: 10px;
}
body::-webkit-scrollbar, .resume::-webkit-scrollbar-thumb {
  background: #006400;
  border-radius: 10px;
}
body::-webkit-scrollbar, .resume::-webkit-scrollbar-thumb:hover {
  background: green; 
}

body {
  background-color: rgba(229, 229, 229, 0.2);
}


